.name {
  font-size: 16px;
  margin: 13px 0 3px;
}
.position {
  font-size: 12px;
}
.intros {
  border-top: 1px solid #dcdcdc;
  border-bottom: 1px solid #dcdcdc;
  margin-top: 10px;
  padding: 15px 0;
}
.intros .item {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  font-size: 13px;
  margin-right: 25px;
}
.intros .item img {
  width: 19px;
  vertical-align: top;
  margin-right: 6px;
}
